* {
  padding: 0;
  margin: 0;
  box-sizing: inherit;
}

body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.heading-primary {
  font-size: 3rem;
  color: #84eaa7;
  text-align: center;
  padding-top: 150px;
  padding-bottom: 20px;
}

.heading-secondary {
  font-size: 2rem;
  color: white;
  text-align: center;
  padding: 6.25rem 0;
}

.description-text {
  color: white;
  font-size: 1.5rem;
  text-align: left;
}

.Global__container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 0.9375rem;
  width: 60%;
  margin: 0 auto;
}

@media only screen and (max-width: 600px) {
  .description-text {
    color: white;
    font-size: 1rem;
    text-align: left;
  }

  .heading-primary {
    font-size: 2.5rem;
    color: #84eaa7;
    text-align: center;
    padding-top: 150px;
    padding-bottom: 20px;
  }

  .Global__container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    padding: 0.9375rem;
    width: 80%;
    margin: 0 auto;
  }
}

.App {
  text-align: center;
}

.App-header {
  background-color: white;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
}

.App-link {
  color: #61dafb;
}

.Image {
  width: 30px;
  height: 30px;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Form {
  background-color: #dddddd;
  color: black;
  width: 300px;
}

h2:active {
  color: red;
}

